.custom-input{
	position: relative;
	left: 10px;
	background: none;
	border-bottom: 1px solid red;
	padding: 6px 25px 6px 10px;
	color: var(--icon-color);
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease 0s;
	&._active{
		opacity: 1;
		left: 0px;
		visibility: visible;
	}
}